
.app {
    width: 100vw;
    height: 100vh;
  }
  
  .sidepanel {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    height: 100vh;
    width: 23vw;
    /* display: none; */
    padding: 40px;
    transition: 1s;
    background-color: #4baca3cc;
  }
  
  .sidepanel a:hover {
    color: #f1f1f1;
  }
  
  .sidepanel .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }

  .sidepanel {
    color: rgb(255, 255, 255);
    font-family: Futura, Helvetica Neue, sans-serif;
    font-size: 18px;
    font-weight: 300;
  }